import back from "../../components/back.vue";
export default {
	data() {
		return {
			vercodeFlag:0,
			loadingFlag:false,
			phoneNum:'',
			errorMsg:'',
			showFlag:0,
			loginType:1 ,//0-验证码登录 1-账号密码
			password:'',
			passwordFlag:true,
			seeFlag:0,
			resetFlag:0,
			backShowFlag:0,
			rememberFlag:1,
			disabledFlag:false
		}
	},
	methods: {
		changeRememberFlag(){
			var that = this;
			if(that.rememberFlag==0){
				that.rememberFlag=1;
			}else{
				that.rememberFlag=0
			}
		},
		changeClass(e){
			var that = this;
			var length = e.detail.value.length;
			var loginType =that.loginType;
			if(length>0){
				that.resetFlag=1
				that.phoneNum=e.detail.value;
			}else{
				that.resetFlag=0;
				that.phoneNum='';
			}
			if(length>=10){
				if(loginType==0){
					that.vercodeFlag=1;
				}else{
					if(that.password!=null&&that.password!=''){
						that.vercodeFlag=1;
					}
				}
			}else{
				that.vercodeFlag=0;
			}
		},
		resetPhoneNum(){
			this.phoneNum="";
			this.resetFlag=0;
			this.vercodeFlag=0;
		},
		changePasswordClass(e){
			var that = this;
			var length = e.detail.value.length;
			var phoneNum =that.phoneNum;
			if(length>0){
				that.password=e.detail.value;
				that.seeFlag=1;
				if(phoneNum!=null&&phoneNum!=''){
					that.vercodeFlag=1;
				}
			}else{
				that.vercodeFlag=0;
				that.password='';
				that.seeFlag=0;
			}
		},
		reset(){
			this.password='';
			this.passwordFlag=true;
			this.seeFlag=0;
			this.vercodeFlag=0;
		},
		see(flag){
			this.passwordFlag=flag;
		},
		getVercode(){
			var that = this;
			that.disabledFlag=true;
			that.loadingFlag=true;
			if(that.phoneNum.length!=11){
				that.showFlag=1;
				that.errorMsg='请检查号码';
				that.loadingFlag=false;
				that.disabledFlag=false;
				return;
			}
			uni.request({
				url: that.serverUrl+'/user/authCode', 
				method:'POST',
				data: {
				   phone:that.phoneNum,
				   devCode:that.cid(),
				   type:0
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				sslVerify:false,
				success: (res) => {
					that.disabledFlag=false;
					that.loadingFlag=false;
					if(res.data.resultCode=='10000'){
						uni.navigateTo({
							url:'../vercode/vercode?phoneNum='+that.phoneNum,
						})
					}else{
						that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
						that.showFlag=1
						setTimeout(function() {
							that.errorMsg='';
							that.showFlag=0;
						}, 5000);
					}
				},
				fail:(re)=>{
					that.loadingFlag=false;
					that.disabledFlag=false;
					uni.showModal({
						title: '提示',
						content: '网络异常，请检查网络后重试',
						showCancel:false,
						success: function (res) {
							if (res.confirm) {
								console.log('用户点击确定');
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				}
			});
		
		},
		loginIn(account,password){
			var that = this;
			that.loadingFlag=true;
			that.disabledFlag=true;
			if(account.length!=11){
				that.showFlag=1;
				that.errorMsg='请检查号码';
				that.disabledFlag=false;
				that.loadingFlag=false;
				return;
			}
			
			uni.request({
			    url: that.serverUrl+'/user/login', 
				method:'POST',
			    data: {
			       phone:account,
				   devCode:that.cid(),
				   key:password,
				   type:1
			    },
			    header: {
			        'content-type': 'application/x-www-form-urlencoded'
			    },
				sslVerify:false,
			    success: (res) => {
					that.disabledFlag=false;
					that.loadingFlag=false;
					if(res.data.resultCode=='10000'){
						if(that.rememberFlag==1&&that.phoneNum!=null&&that.phoneNum!=undefined&&that.phoneNum!=''&&that.password!=null&&that.password!=undefined&&that.password!=''){
							uni.setStorageSync("account",that.phoneNum);
							uni.setStorageSync("password",that.password);
						}
						uni.setStorageSync("loginTime",new Date())
						var userInfo = eval(res.data.data);
						userInfo.phoneNum=that.phoneNum;
						uni.setStorageSync('userInfo',userInfo);
						// console.log(userInfo.userId)
						uni.switchTab({
							url:'../information/information',
						})
					}else{
						that.errorMsg=that.showError(res.data.msgCode,res.data.msg);
						that.showFlag=1
						setTimeout(function() {
							that.errorMsg='';
							that.showFlag=0;
						}, 5000);
					}
			    },
				fail:(re)=>{
					that.disabledFlag=false;
					that.loadingFlag=false;
					uni.showModal({
					    title: '提示',
					    content: '网络异常，请检查网络后重试',
						showCancel:false,
					    success: function (res) {
					        if (res.confirm) {
					            console.log('用户点击确定');
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}
			});
		},
		changeLoginType(type){
			var that = this;
			that.loginType=type;
			that.loadingFlag=false;
			that.errorMsg='';
			that.showFlag=0;
			that.password='';
			that.passwordFlag=false;
			that.seeFlag=0;
			that.vercodeFlag=0
			if(type==0&&that.phoneNum.length>=10){
				that.vercodeFlag=1
			}
		},
		jumpToForgetPassword(){
			uni.navigateTo({
				url:'../forgetPassword/forgetPassword',
			})
		},
		jumpToFastlogin(){
			uni.navigateTo({
				url: '../fastLogin/fastLogin',
			});
		},
		jumpToUserAgreement(){
			uni.navigateTo({
				url: '../userAgreement/userAgreement',
			});
		},
		jumpToPrivacyAgreement(){
			uni.navigateTo({
				url: '../privacyAgreement/privacyAgreement',
			});
		}
	},
	onLoad() {
		var that=this;
		that.getCid();
		var cid = that.cid();
		if(cid){
			var loginTime = uni.getStorageSync("loginTime");
			if(loginTime){
				if((new Date()-loginTime)/(1000*24*60*60)>30||(new Date()-loginTime)<0){
					uni.removeStorageSync("account");
					uni.removeStorageSync("password");
				}else{
					var account = uni.getStorageSync("account");
					var password = uni.getStorageSync("password");
					if(account!=null&&account!=undefined&&account!=''&&password!=null&&password!=undefined&&password!=''){
						that.phoneNum=account;
						that.password=password;
						that.vercodeFlag=1;
						that.resetFlag=1;
						that.seeFlag=1;
						that.passwordFlag=1;
						that.loginIn(account,password)
					}
				}
			}
		}else{
			setTimeout(function(){
			var loginTime = uni.getStorageSync("loginTime");
			if(loginTime){
				if((new Date()-loginTime)/(1000*24*60*60)>30||(new Date()-loginTime)<0){
					uni.removeStorageSync("account");
					uni.removeStorageSync("password");
				}else{
					var account = uni.getStorageSync("account");
					var password = uni.getStorageSync("password");
					if(account!=null&&account!=undefined&&account!=''&&password!=null&&password!=undefined&&password!=''){
						that.phoneNum=account;
						that.password=password;
						that.vercodeFlag=1;
						that.resetFlag=1;
						that.seeFlag=1;
						that.passwordFlag=1;
						that.loginIn(account,password)
					}
				}
			}
		},2000)
		}
	},
	components: {
		back
	}
}
